input {
  display: none;
  visibility: hidden;
}

#PROJECTS {
  height: 0px;
  overflow: hidden;
  -webkit-transition: height 0.5s;
  -o-transition: height 0.5s;
  transition: height 0.5s;
}

label::before {
  content: "+";
}

#toggle:checked ~ #PROJECTS {
  height: 250px;
}

#toggle:checked ~ label::before {
  content: "-";
}



